import React, { PureComponent } from 'react'
import { Timeline } from 'react-twitter-widgets'
import {Helmet} from "react-helmet";

export default class SocialMedia extends PureComponent {
    styleObj = {
        display: "inline-block",
        fontFamily: "Helvetica Neue, Roboto, Segoe UI, Calibri, sans-serif",
        fontSize: "12px",
        // fontWeight: "bold",
        lineHeight: "16px",
        borderColor: "#eee #ddd #bbb",
        borderRadius: "5px",
        // borderStyle: "solid",
        // borderWidth: "1px",
        // boxShadow: "0 1px 3px rgba(0, 0, 0, 0.15)",
        margin: "0 auto",
        padding: "0",
        width: "50%",
        minWidth: "375px",
        maxWidth: "600px",
        // textAlign: "center",
    }

    LinkRenderer = (props) => <a href={props.href} target="_blank" rel="noopener noreferrer">{props.children}</a>
    
    render() {
        const isEn = this.props.lang==="en";
        return (
            <>
                <Helmet>
                    <title>{ isEn ? "Twitter - Tao Jiang" : "蒋韬的推特"}</title>
                    <meta name="description" content={ isEn ? "Tao Jiang's Twitter timeline" : "蒋韬的推特时间线" }/>
                </Helmet>
                <blockquote style={this.styleObj}>
                    {/* Timeline (with options) */}
                    <Timeline
                        dataSource={{
                            sourceType: 'profile',
                            screenName: 'TaoJiangScholar'
                        }}
                        renderError={_err =>
                            <div id='maincontent-container'>
                                <div style={{ fontSize: "small", width: "100%", padding: "2em" }}>
                                    <p>
                                        { isEn
                                            ? "Could not load Twitter content due to browser privacy setting or internet connection."
                                            : "无法加载推特内容，请检查网络连接或浏览器隐私设置。"
                                        }
                                    </p>
                                    { isEn
                                        ?  <p>Please check out <a href="https://twitter.com/taojiangscholar" target="_blank" rel="noreferrer">Tao Jiang on Twitter</a>.</p>
                                        :  <p>访问<a href="https://twitter.com/taojiangscholar" target="_blank" rel="noreferrer">蒋韬的推特主页</a>.</p>
                                    }
                                </div>
                            </div>
                        }
                        options={{
                            chrome: "nofooter",
                            // width: 600,
                            // minHeight: 600,
                            dnt: true
                        }}
                    />
                </blockquote>
            </>
        )
    }
}